<template>
  <div>
    <div class="m-container">
      <view-header title="医生介绍"></view-header>
      <div class="box">
        <div class="header">
          <p class="title">基本信息</p>
          <img class="edit-icon" src="static/img/edit-icon.png" v-if="input.length !== 0" @click="dialogVisible1 = true"/>
        </div>
        <div class="add" @click="dialogVisible1 = true" v-if="input.length === 0">
          <img class="icon" src="static/img/add-icon.png"/>
          <p class="tip">点击添加专家基本信息</p>
        </div>
        <div class="add" v-else>
          <div class="content">
            <!-- <div class="left">
              <div class="icon"></div>
            </div> -->
            <div class="right">
              <p class="item" style="color: #1AC7D8">医生名称：{{doctorName}}</p>
              <p class="item" style="color: #1AC7D8">医生职称：{{docPosition}}</p>
              <p class="item" style="color: #1AC7D8">医学之路：{{docSummery}}</p>
              <p class="item" style="color: #1AC7D8">所获荣誉：{{docHonor}}</p>
            </div>
          </div>
        </div>
      </div>
      <!-- <div class="box">
        <div class="header">
          <p class="title">专家介绍</p>
          <img class="add-content" src="static/img/add-content.png" v-if="introList.length > 0" @click="dialogVisible2 = true"/>
        </div>
        <div class="add" @click="dialogVisible2 = true" v-if="introList.length === 0">
          <img class="icon" src="static/img/add-icon.png"/>
          <p class="tip">点击添加专家介绍</p>
        </div>
        <div class="introList" v-else v-for="(item, index) in introList" :key="index">
          <div class="header">
            <p class="title">{{item.title}}</p>
            <img class="edit-icon" src="static/img/edit-icon.png" @click="dialogVisible2 = true"/>
          </div>
          <div v-html="item.content" style="width: 100%"></div>
        </div>
      </div> -->
    </div>
    <el-dialog
      title="医生介绍"
      :visible.sync="dialogVisible1"
      width="80%">
      <div class="dialog">
        <!-- <div class="left">
          <img class="icon" src="static/img/add-icon.png"/>
          <p class="tip">头像</p>
          <input class="file" type="file"/>
        </div> -->
        <div class="right">
          <div class="item">
            <p><span>*</span>专家姓名:</p>
            <el-input v-model="doctorName" placeholder="请输入内容" readonly></el-input>
          </div>
          <div class="item">
            <p><span>*</span>职称:</p>
            <el-input v-model="docPosition" placeholder="请输入内容"></el-input>
          </div>
          <div class="item">
            <p><span>*</span>社会荣誉:</p>
            <el-input v-model="docHonor"
            type="textarea"
            rows="5"
            resize="none"
            placeholder="请输入内容"></el-input>
          </div>
           <div class="item">
            <p><span>*</span>医学之路:</p>
            <el-input v-model="docSummery"
            type="textarea"
            rows="5"
            resize="none"
            placeholder="请输入内容"></el-input>
          </div>
        </div>
      </div>
      <footer class="footer">
        <button class="btn" @click="_updateDoctor()">保存</button>
      </footer>
    </el-dialog>
    <!-- <el-dialog
      title="专家介绍"
      :visible.sync="dialogVisible2"
      width="80%">
      <div class="dialogInfor">
        <div class="item">
          <p><span>*</span>标题:</p>
          <el-input v-model="details.title" placeholder="请输入内容"></el-input>
        </div>
        <div class="item">
          <p><span>*</span>内容:</p>
          <quill-editor
            v-model="details.content"
            :options="editorOption"
            @change="_onEditorChange($event)">
          </quill-editor>
        </div>
      </div>
      <footer class="footer">
        <button class="btn" @click="_add">删除</button>
        <button class="btn" @click="_add">保存</button>
      </footer>
    </el-dialog> -->
  </div>
</template>
<script type="text/ecmascript-6">
import ViewHeader from 'base/view-header/view-header'
import {querydoctor,updatedoctor} from '@/api'
import {quillEditor} from 'vue-quill-editor'
export default {
  components: {
    ViewHeader,
    quillEditor
  },
  mounted () {
    var _this = this
    var params = new URLSearchParams()
    querydoctor(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
         _this.docPosition = data.data.positionName
         _this.docSummery = data.data.doctorSummary
         _this.docHonor = data.data.doctorHonor
        }
      }).catch(function (data) {
        alert(data)
      })
  },
  data () {
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      input: '1',
      details: {
        title: '',
        content: ''
      },
      editorOption: {},
      introList: [],
      docPosition: '',
      docSummery: '',
      docHonor: '',
      doctorName: '朱志清'
    }
  },
  methods: {
     _updateDoctor () {
        var _this2 = this
        var params = new URLSearchParams()
        params.append('docPosition', _this2.docPosition)
        params.append('docSummery', _this2.docSummery)
        params.append('docHonor', _this2.docHonor)
        updatedoctor(params).then(function (data) {
        if (!data.success) {
          alert(data.error)
        } else {
          _this2.dialogVisible1= false
        }
      }).catch(function (data) {
        alert('系统异常，请稍后再试')
      })
     }
    // _onEditorChange () {
    // },
    // _add () {
    //   this.introList.push(JSON.parse(JSON.stringify(this.details)))
    //   this.dialogVisible2 = false
    // }
  }
}
</script>
<style lang="stylus" scoped rel="stylesheet/stylus">
@import "~common/stylus/base"
@import "~common/stylus/variable"
.m-container
  @extend .flex
  @extend .flex-column
  width: 100%
  height: 100%
  padding: 0 10px
  .box
    width: 100%
    border: 1px solid $color-back-gray_2
    padding: 20px
    background-color: $color-back-white_0
    margin-bottom: 20px
    .header
      @extend .flex
      @extend .flex-row
      @extend .flex-between
      margin-bottom: 10px
      .title
        font-size: $size-16
        color: $color-back-black_1
      .edit-icon
        @extend .pointer
        width: 32px
        height: 32px
      .add-content
        @extend .pointer
        width: 24px
        height: 24px
    .introList
      @extend .flex
      @extend .flex-column
      width: 100%
      margin-bottom: 20px
    .add
      @extend .flex
      @extend .flex-column
      @extend .flex-center
      width: 100%
      height: 160px
      .header
        @extend .flex
        @extend .flex-row
        @extend .flex-between
        width: 100%
      .title
        font-size: $size-14
        color: $color-back-black_1
      .icon
        @extend .pointer
        width: 40px
        height: 40px
      .tip
        font-size: $size-14
        color: $color-back-black_1
        margin-top: 20px
      .content
        @extend .flex
        @extend .flex-row
        width: 100%
        .left
          @extend .flex
          @extend .flex-center
          width: 200px
          .icon
            @extend .pointer
            width: 120px
            height: 120px
            border-radius: 60px
            padding: 40px
            border: 1px solid $color-back-blue_0
        .right
          @extend .flex
          @extend .flex-column
          @extend .flex-center
          width: calc(100% - 200px)
          .item
            width: 100%
            margin-bottom: 20px
            font-size: $size-14
            color: $color-back-black_1
.dialog
  @extend .flex
  @extend .flex-row
  width: 100%
  .left
    @extend .flex
    @extend .flex-center
    @extend .flex-column
    width: 200px
    position relative
    .icon
      @extend .pointer
      width: 120px
      height: 120px
      border-radius: 60px
      padding: 40px
      border: 1px solid $color-back-gray_2
    .tip
      font-size: $size-14
      color: $color-back-black_1
      margin-top: 20px
    .file
      position absolute
      width 100%
      height 100%
      top 0px
      left 0px
      opacity 0
  .right
    @extend .flex
    @extend .flex-column
    width: calc(100% - 200px)
    .item
      width: 100%
      margin-bottom: 20px
      span
        color: $color-back-blue_2
.footer
  @extend .flex
  @extend .flex-end
  width: 100%
.btn
  border: 0px
  background-color: $color-back-blue_0
  color: $color-back-white_0
  font-weight: bolder
  padding: 0 35px
  margin-left: 10px
  height: 40px
  font-size: $size-16
.dialogInfor
  @extend .flex
  @extend .flex-column
  width 100%
  .item
    width: 100%
    margin-bottom: 20px
    span
      color: $color-back-blue_2
</style>



